/*admin-nav styling*/
#admin-nav{
    height: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    font-size: 1rem;
}

#admin-nav-list-wrapper{
    height: 50%;
    width: 100%;
    display: flex;
    justify-content: center;
    background: #474bff;
    background: -webkit-linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background: linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background-attachment: fixed;
}

#admin-nav-list{
    height: 100%;
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.admin-nav-item{
    width: 25%;
    text-align: center;
    font-weight: 350;
    list-style-type: none;
}

.admin-nav-item a{
    color: #fff;
    text-decoration: none;
}

.nav-hr{
    display: block;
    height: 2.3px;
    background: #ff008d;
    width: 0;
    transition: .4s;
}

.admin-nav-item:hover .nav-hr{
    width: 100%;
}

#leagues{
    height: 50%;
    display: flex;
}

.league-image-wrapper{
    width: 33.33%;
}

.league-image-align{
    width: 20%;
    height: 100%;
    margin: 0 auto;
}

.league-image-align img{
   display: block;
   max-width: 100%;
   height: 100%;
   margin: 0 auto; 
   border-radius: 100%;
   transition: .2s;
}

.league-image-align img:hover{
    transform: scale(1.05);
    cursor: pointer;
}

@media screen and (max-width: 920px){
    #admin-nav-list{
        width: 100%;
    }
}

/*on smaller screens, the league images scale down*/
@media screen and (max-width: 425px) {
    .league-image-align {
        width: 59%;
        height: 65%;
        margin: 0 auto;
    }    
}