#admin-main-content{
    display: flex;
    justify-content: flex-end;
    height: 100%;
    position: relative;
    background: #474bff;
    background: -webkit-linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background: linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
}

#showAdmin-info{
    position: fixed;
    top: 50%;
    right: 0;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    padding-bottom: 3%;
    border: 1px solid #fff;
    background: none;
    color: #fff;
    cursor: pointer;
}

/*background attachment here ensures that the divs background gradient stays fixed*/
#videos-articles-section{
    height: 100%;
    width: 80%;
    max-width: 1300px;
    background: #474bff;
    background: -webkit-linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background: linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background-attachment: fixed;
}

.videos-articles-main-container{
    height: 100%;
    background: #474bff;
    background: -webkit-linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background: linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
}

#videos-content{
    padding: 2% 0;
    background: #474bff;
    background: -webkit-linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background: linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
}

/*Styles focusing on video wrapper and children only*/
.videos-wrapper{
    width: 85%;
    margin: 0 auto;
    padding: 1%;
    border-radius: 5px;
    background: #fff;
}

#articles-main-heading, #videos-main-heading{
    padding: 0.5% 0;
    text-align: center;
    color: #1818a7;
}

.videos-latest, .articles-latest{
    width: 90%;
    margin: 0 auto;
}

.videos-latest{
    max-width: 850px;
}

.top-video{
    height: 65%;
    position: relative;
    cursor: pointer;
}

.sub-videos{
    width: 100%;
    cursor: pointer;
}

.video{
    display: flex;
    padding-top: 1.5%;
}

.sub-video-bannerImage-wrapper{
    width: 20%;
    border-radius: 10px;
    position: relative;
}

.videoPlayer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


.videoInfo{
    width: 80%;
    padding: 1%;
    color: #fff;
}

.videoInfoHeading{
    color: #000;
}

.videoInfoHeading:hover{
    text-decoration: underline;
}

.f-articles{
    color: #005aff;
    font-size: 1.1rem;
    padding-right: 1%;
}

.sub-video-imageBanner{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 10px;
}

#bannerVideo{
    height: 100%;
    width: 100%;
    border-radius: 10px;
}

.bannerVideo-text{
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    color: #fff;
    text-align: center;
    overflow: hidden;
    border-radius: 10px;
}

.hideBanner{
    z-index: -1;
    position: absolute;
    height: 100%;
    top: 0;
    width: 100%;
    color: #fff;
    text-align: center;
}

.videoImage{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
    transition: .4s;
}

/*102% width and height to ensure the image grows beyond its normalities, but still hides the overflow content*/
.bannerVideo-text:hover .videoImage{
    display: block;
    margin: auto;
    width: 102%;
    height: 102%;
}

.bannerVideo-heading{
    transition: .4s;
}

.bannerVideo-heading:hover{
    transform: scale(1.03);
}

.video-player{
    margin: 0 auto;
    width: 30px;
    height: 30px;
    padding: 2%;
    background: #ff008d;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/*ensuring that the banner video Image comes first*/
.videoOverlay{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    background: #00317059;
    border-radius: 10px;
}

.videoOverlayText{
    width: 70%;
}

.sub-video-player{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.fa-play{
    color: #fff;
}




/*Styles focusing on article wrapper and children only*/
#articles-content{
    height: 100%;
    background-attachment: fixed;
    background: #474bff;
    background: -webkit-linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
    background: linear-gradient(79deg, #474bff 0%, #bc48ff 100%);
}

.articles-wrapper{
    height: 100%;
    width: 85%;
    margin: 0 auto;
    border-radius:5px;
    background: #fff;
}

#articles-main-heading{
    height: 5%;
}

.articles-latest{
    margin: 0 auto;
    height: 95%;
}

.top-article{
    position: relative;
    cursor: pointer;
    height: 40%;
}

.sub-articles{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 100%;
    height: 60%;
    cursor: pointer;
}

.articlesBannerImage{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

.articlesBannerImage-text{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    background: #00317059;
    border-radius: 10px;
}

#bannerImage-heading{
    transition: .4s;
}

#bannerImage-heading:hover{
    transform: scale(1.03);
    text-decoration: underline;
}

.image{
    padding: 2%;
}

.imageInfoHeading{
    transition: .4s;
}

.imageInfoHeading:hover{
    transform: scale(1.03);
    text-decoration: underline;
}

.sub-image-bannerImage-wrapper{
    height: 100%;
    position: relative;
}

.sub-image-imageBanner{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

.imageInfo{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    background: #00317059;
    border-radius: 10px;
}








/*RESPONSIVE ACTIVITY -- styles apply to tablets down*/
@media screen and (max-width: 920px)  {
    
    /*focusing on the articles*/
    .sub-articles{
        display: block;
    }

    .image{
        height: 50%;
    }

    .bannerVideo-heading, #bannerImage-heading, .imageInfoHeading{
        font-size: 1.1rem;
    }

    .videoInfoHeading{
        font-size: 0.8rem;
    }

    .videoSummary{
        font-size: 0.75rem;
    }

    .video-player{
        width: 15px;
        height: 15px;
        padding: 10%;
    }

    .fa-play{
        font-size: 0.5rem;
    }
}


/*reponsiveness to the video player size*/
@media screen and (min-width: 426px) and (max-width: 920px) {
    .video-player{
        width: 30px;
        height: 30px;
        padding: 5%;
    }
}

/*mobile responsiveness to reveal all article images on mobile phones*/
@media screen and (max-width: 425px) {
    .articles-wrapper{
        padding-bottom: 10%;
    }
}